@import "src/global"

\:root
  --background: #0C0C0C
  --gary-0: #101315
  --gary-1: #171F26
  --gary-2: #3A3D46
  --gary-3: #656B7D
  --gary-4: #6b7286
  --accent: #2f00ff
  --white: #E6E6E6

*
  box-sizing: border-box
  padding: 0
  margin: 0
  border: 0
  background: none
  color: inherit
  font-size: inherit
  line-height: inherit
  width: auto
  -webkit-tap-highlight-color: transparent

  &::selection
    color: var(--white)
    background-color: var(--accent)

body
  margin: 0
  background-color: var(--background)
  color: var(--white)
  user-select: none
