@import "src/global"

.tag
  position: relative
  width: fit-content
  height: px(28)
  display: flex
  align-items: center
  justify-content: center
  gap: px(4)
  padding: px(0) px(12)
  background-color: var(--gary-1)
  cursor: pointer
  border-radius: px(999)
  white-space: nowrap
  overflow: hidden
  @include text-14
  @include button-hover

  &:after
    position: absolute
    right: px(-28)
    content: ""
    min-width: px(28)
    height: px(28)
    display: flex
    background-size: contain
    background-image: url("./images/black-close-icon.svg")
    z-index: 5
    transform: scale(0)
    transition: 0.15s ease right, 0.15s ease transform

  &__checkbox
    opacity: 0
    position: absolute
    cursor: pointer

  &_medium
    height: px(36)

  &_turned-on
    padding-right: px(32) !important
    background-color: var(--white) !important
    color: var(--background) !important

    &:after
      right: px(0)
      transform: scale(1)

.tag_turned-on.tag_medium
  padding-right: px(36) !important

  &:after
    right: px(4)
