@mixin button-hover
  transition: 0.15s ease transform, 0.15s ease opacity, 0.15s ease background-color, 0.15s ease width, 0.15s ease margin

  &:active
    transition: 0.15s ease transform, 0.15s ease background-color
    transform: scale(0.95)

  &:before
    content: ""
    position: absolute
    width: calc(100% - #{px(5)})
    height: calc(100% - #{px(5)})
    margin: px(1)
    left: 0
    top: 0
    pointer-events: none
    border: px(2) solid var(--background)
    border-radius: px(999)
    opacity: 0
    transition: 0.15s ease transform, 0.15s ease opacity, 0.15s ease background-color, 0.15s ease width, 0.15s ease margin

  @media screen and (pointer: fine)
    &:hover
      background-color: var(--gary-2)
      transition: 0.15s ease transform, 0.15s ease opacity, 0.15s ease background-color, 0.15s ease width, 0.15s ease margin

    &:hover:before
      opacity: 1
      transition: 0.15s ease transform, 0.15s ease opacity, 0.15s ease background-color, 0.15s ease width, 0.15s ease margin

  &:active:before
    transition: 0.15s ease transform, 0.15s ease opacity, 0.15s ease background-color, 0.15s ease width, 0.15s ease margin
    width: 100%
    height: 100%
    margin: px(-1)