@import "src/global"

.preloader
  width: px(100)
  height: px(100)
  display: flex
  align-items: center
  justify-content: center

  &__icon
    width: px(48)
    height: px(48)
    display: block
    animation: preloader-animation 1.5s ease infinite

@keyframes preloader-animation
  0%
    transform: rotate(0deg)
  100%
    transform: rotate(360deg)